import React, { Component } from 'react';
import { Modal, Form, Badge } from 'react-bootstrap';
import ProfileImage from '../../CommonComponents/ProfileImage/ProfileImage';
import { connect } from 'react-redux';
import { toast } from 'react-toastify';
import HttpClient from '../../../../Services/http-client';
import { COLLBORATOR_CREATE_API } from '../../../../Services/Api';
const NoDataFound = React.lazy(() => import('../../CommonComponents/ErrorPage/NoDataFound'))
class CollaboratorModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedOption: null,
            isLoading: false,
            recommendedList:[],
            copyRecommendedList:[],
            copyAccessList:[],
            accessList:[],
            collectionId:null,
            selectedCollaborator:null
        };
    }

    handleClose = () => {
        this.setState({isLoading:false,selectedCollaborator:null},()=>this.props.collaboratorCallback(false))
    };

    createCollaborator = () => this.props.history("collaborator/invite")

    componentWillReceiveProps(nextProps){
        const { collaboratorList } = nextProps
        if(nextProps.collectionId != this.state.collectionId) {
            const List = collaboratorList.length > 0 ? collaboratorList.filter(collab=> collab.permissions!='' && !collab.permissions.ugc_library.collectionId.includes(String(nextProps.collectionId))) : null
            const access = collaboratorList.length > 0 ? collaboratorList.filter(collab=> collab.permissions=='' || collab.permissions.ugc_library.collectionId.includes(String(nextProps.collectionId))): null
            this.setState({collectionId:nextProps.collectionId, recommendedList:List,isLoading:false,selectedCollaborator:null, copyRecommendedList:List, accessList:access, copyAccessList:access})
        }
    }
    onSelectCollaborator = (item) =>{
        this.setState({selectedCollaborator:item})
    }
    onSearchCollaborator = (event) =>{
        const { copyRecommendedList, copyAccessList } = this.state
        const recommendResults = copyRecommendedList.filter(collab=> collab['UserCollab.emailId'].toLowerCase().includes(event.target.value.toLowerCase()))
        const activeResults = copyAccessList.filter(collab=> collab['UserCollab.emailId'].toLowerCase().includes(event.target.value.toLowerCase()))
        this.setState({recommendedList:recommendResults, accessList:activeResults})
    }

    onSubmit = () =>{
        const { selectedCollaborator, collectionId } = this.state
        if(selectedCollaborator){
            this.setState({isLoading:true})
            const {permissions} = selectedCollaborator
            const payload = {
                emailId: selectedCollaborator['UserCollab.emailId'],
                userType: 2,
                workSpace: selectedCollaborator.workspace,
                permission:{
                    ugc_library:{
                        createCollection: permissions.ugc_library.createCollection,
                        collectionId: String(permissions.ugc_library.collectionId) + ',' + String(collectionId)
                    },
                    publish:{
                        widget: permissions.publish.widget,
                        hashtag_camp : permissions.publish.hashtag_camp,
                        d_screens:permissions.publish.d_screens,
                        email_camp: permissions.publish.email_camp,
                        social_publish: permissions.publish.social_publish,
                        print_media: permissions.publish.print_media
                    },
                    networkManager:{
                        manageAccount: permissions.networkManager.manageAccount,
                        manageConnection : permissions.networkManager.manageConnection
                    },
                    otherSettings:{
                        styleStudio: permissions.otherSettings.styleStudio,
                        autoPilot: permissions.otherSettings.autoPilot,
                        creators: permissions.otherSettings.creators,
                        insights: permissions.otherSettings.insights,
                        trash: permissions.otherSettings.trash
                    }
                }
            }
            new HttpClient().post(COLLBORATOR_CREATE_API, payload).then(response =>{
                const { response_code } = response.data;
                if(response_code == 200) {
                    toast.success("Collection shared successfully.")
                    this.setState({isLoading:false},()=>this.handleClose())
                }}).catch(ex=>{this.setState({isLoading:false},()=>{})})
        }
        else {toast.error("Please select collaborator.")}
    }
    render() {
        const { collaboratorShowModal } = this.props
        const {isLoading, recommendedList, selectedCollaborator, copyRecommendedList, accessList} = this.state
        return <Modal show={collaboratorShowModal} onHide={this.handleClose}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title>Share Collection</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="connection_modal mx-xl-4 px-2 px-lg-4 mx-0" style={{maxHeight:'calc(100vh - 150px)'}}>
                    {copyRecommendedList && copyRecommendedList.length > 0 && <Form.Control size='lg' className="mb-3" placeholder='Search email address' onChange={event=>this.onSearchCollaborator(event)}/>}
                    {(recommendedList && recommendedList.length > 0) || (accessList && accessList.length > 0) ? 
                        <>
                        {recommendedList && recommendedList.length > 0 ? <> <h4 className='mb-2 fs-6'>Recommended Collaborators</h4>
                         <div className='mb-3'>
                            {recommendedList.map((collab,index)=>{
                            return <div className={`d-flex justify-content-between align-items-center p-2 rounded-1 mb-2 cursor-pointer border border-${selectedCollaborator && selectedCollaborator.id == collab.id ? 'success' : 'secondary'}`} key={index} onClick={()=>this.onSelectCollaborator(collab)}>
                                <div className="d-flex align-items-center flex-grow-1 me-2 w-50">
                                    <ProfileImage className={'symbol symbol-40 rounded-circle flex-shrink-0 me-2 overflow-hidden bg-light'}
                                        imageSize={40}
                                        symbolClass={'symbol-label text-light-alt fw-bolder text-uppercase fs-5'}
                                        name={'Image'} profileUrl={ collab['UserCollab.picture'] ? collab['UserCollab.picture'] : ''} networkId={''} />
                                    <div className="overflow-hidden">
                                        <p className="text-gray-800 mb-0 w-100 fw-bold text-ellipsis">{collab["UserCollab.name"]}</p>
                                        <p className="mb-0 text-gray-600 text-ellipsis mb-0 fs-8">
                                            {collab["UserCollab.emailId"]}
                                        </p>
                                    </div>
                                </div>
                                <div className="d-flex flex-column align-items-end flex-srink-0 w-25">
                                <Badge className='py-2 px-3' bg="light-primary">
                                {collab["Workspace.name"]}
                                </Badge> 
                                </div>
                                <div className="d-flex flex-column align-items-end flex-srink-0 w-25">
                                    <div className="fs-8 text-muted fw-medium">Manager</div>
                                </div>
                            </div>})}
                        </div> </> :  null}
                        {accessList && accessList.length > 0  ? <> <h4 className='mb-2 fs-6'>Collaborators with Access.</h4>
                        <div className='mb-3'>
                            {accessList.map((collab,index)=>{
                            return <div className={`d-flex justify-content-between align-items-center p-2 rounded-1 mb-2 cursor-pointer border border-${selectedCollaborator && selectedCollaborator.id == collab.id ? 'success' : 'secondary'}`} key={index}>
                                <div className="d-flex align-items-center flex-grow-1 me-2 w-50">
                                    <ProfileImage className={'symbol symbol-40 rounded-circle flex-shrink-0 me-2 overflow-hidden bg-light'}
                                        imageSize={40}
                                        symbolClass={'symbol-label text-light-alt fw-bolder text-uppercase fs-5'}
                                        name={'Image'} profileUrl={ collab['UserCollab.picture'] ? collab['UserCollab.picture'] : ''} networkId={''} />
                                    <div className="overflow-hidden">
                                        <p className="text-gray-800 mb-0 w-100 fw-bold text-ellipsis">{collab["UserCollab.name"]}</p>
                                        <p className="mb-0 text-gray-600 text-ellipsis mb-0 fs-8">
                                            {collab["UserCollab.emailId"]}
                                        </p>
                                    </div>
                                </div>
                                <div className="d-flex flex-column align-items-end flex-srink-0 w-25">
                                <Badge className='py-2 px-3' bg="light-primary">
                                {collab["Workspace.name"]}
                                </Badge> 
                                </div>
                                <div className="d-flex flex-column align-items-end flex-srink-0 w-25">
                                    <div className="fs-8 text-muted fw-medium">{collab.role == 1 ? "Admin" : "Manager"}</div>
                                </div>
                            </div>})}
                        </div> </>: null}
                        </>
                    :
                    <NoDataFound title="Uh-oh! Nothing to see here." desc="You can start adding new collaborators here." icon="invoice-placeholder" method={this.createCollaborator} methodTitle={"Invite Collaborator"} headSize={'300px'} containerSize={450} iconHeight={32} iconWidth={32} lgIcon={true}/>
                    }
                </div>
            </Modal.Body>
            <Modal.Footer className="p-3">
                <div className="mx-xl-4 px-2 px-lg-4 mx-0">
                    <button type="button" className="btn btn-secondary btn-sm btn-min-w" onClick={this.handleClose}>Cancel</button>
                    {recommendedList && recommendedList.length > 0 ?
                    <button type="button" className={`btn btn-primary btn-sm btn-min-w ms-3 ${isLoading ? `spinner` : ``}`} onClick={this.onSubmit}>Share</button>
                    :null}
                </div>
            </Modal.Footer>
        </Modal>
    }
}

const mapStateToProps = state => {
    return {
        collaboratorList: state.collaborator.collaboratorList,
        userData: state.authenticated.userData,
    }
}


export default connect(mapStateToProps)(CollaboratorModal)